<template>
	<view class="container">
		<view class="title">Á∫¢ËñØüç†</view>
		<view class="info">
			<view class="center">
				<input placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑" class="info-input" v-model="userInfo.phone" />
				<input placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å" class="info-input" type="password" v-model="userInfo.password" />
			</view>
			<view class="info-bottom">
				<view @click="loginByCode">È™åËØÅÁ†ÅÁôªÂΩï</view>
				<view class="info_right">
					<view @click="findPassword">ÂøòËÆ∞ÂØÜÁ†ÅÔºü</view>
					<!-- <view @click="regist">Ê≥®ÂÜå</view> -->
				</view>

			</view>
			<view class="login">
				<button @click='login'>ÁôªÂΩï</button>
			</view>
		</view>
		<view class="fotter">
			<tui-divider width="80%">ÂÖ∂‰ªñÊñπÂºèÁôªÂΩï</tui-divider>
			<view class="other-login">
				<view class="other-item">
					<view class="icon">
						<image src="/static/images/share/icon_wechat.png" data-loginType="weixin" @click="auth"></image>
					</view>
					<view class="font">ÂæÆ‰ø°</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		login,
		otherLogin
	} from "@/api/login.js"
	import {
		tokenUtil
	} from "@/utils/token.js"
	import socket from 'plus-websocket'
	import {
		appConfig
	} from '@/config/config.js'
	import {
		isMobile
	} from '@/utils/validate.js'
	export default {
		data() {
			return {
				userInfo: {},
			}
		},

		created() {
			uni.hideTabBar()
		},

		methods: {
			login() {
				let isPhone = isMobile(this.userInfo.phone)
				if (isPhone) {
				} else {
					uni.showToast({
						title: "ËØ∑ËæìÂÖ•Ê≠£Á°ÆÊâãÊú∫Âè∑ÔΩû",
						icon: 'none'
					})
					return
				}
				if (this.userInfo.phone == null || this.userInfo.password == null) {
					uni.showToast({
						title: "‰ø°ÊÅØËæìÂÖ•ÊúâËØØ,ËØ∑Ê£ÄÊü•ÊâãÊú∫Âè∑ÂØÜÁ†Å~"
					})
				} else {
					login(this.userInfo).then(res => {
						if (res.data.res == 0) {
							uni.showToast({
								title: res.data.message
							})
						} else {
							let user = res.data.userInfo
							tokenUtil.set(res.data.accessToken)
							tokenUtil.setUserId(res.data.userInfo.id)
							uni.setStorageSync("userInfo", user)
							setTimeout(() => {
								uni.reLaunch({
									url: "/pages/index/index"
								});
							}, 500)
						}

					})
				}
			},

			regist() {
				uni.navigateTo({
					url: "/pages/regist/regist"
				})
			},

			loginByCode() {
				uni.navigateTo({
					url: "/pages/login/loginByCode"
				})
			},

			findPassword() {
				uni.navigateTo({
					url: "/pages/login/findPassword"
				})
			},

			auth(e) {
				//‰∏ÄÂÆöË¶ÅÁî®that
				let that = this

				let loginType = e.currentTarget.dataset.logintype

				uni.login({
					provider: loginType,
					success(res) {
						// ÁôªÂΩïÊàêÂäüÂêé, Ëé∑ÂèñÁî®Êà∑Êï∞ÊçÆ
						uni.getUserInfo({
							provider: loginType,
							success(info) {
								var userInfo = info.userInfo
								var nickName = ""
								var avatarUrl = ""
								var openId = ""
								if (loginType == "weixin") {
									nickName = userInfo.nickName
									avatarUrl = userInfo.avatarUrl
									openId = userInfo.openId
								} else if (loginType == "qq") {
									nickName = userInfo.nickname
									avatarUrl = userInfo.figureurl_qq_2
									// qqËøîÂõû‰∫ÜÂ§ö‰∏™Â∞∫ÂØ∏ÁöÑÂ§¥ÂÉè, ÊåâÈúÄÈÄâÊã©
									openId = userInfo.openId
								} else if (loginType == "sinaweibo") {
									nickName = userInfo.nickname
									avatarUrl = userInfo.avatar_large
									openId = userInfo.id
								}

								let userOtherLoginRelationDTO = {}
								userOtherLoginRelationDTO.otherUserId = openId
								userOtherLoginRelationDTO.otherUsername = nickName
								userOtherLoginRelationDTO.otherAvatar = avatarUrl
								userOtherLoginRelationDTO.otherToken = res.access_token

								otherLogin(userOtherLoginRelationDTO).then(res => {

									let user = res.data.userInfo
									tokenUtil.set(res.data.JwtToken)
									uni.setStorageSync("userInfo", user)

									setTimeout(() => {
										uni.reLaunch({
											url: "/pages/index/index"
										});
									}, 500)

								})

							}
						})
					}
				})
			},
		}
	}
</script>

<style scoped>
	@import url(./css/login.css);
</style>